* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-size: cover;
  width: 100%;
  height: 100vh;
}
a {
  text-decoration: none;
}
.App {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

header {
    width: 100%;
    height: 60px;
    box-shadow: 0px 0px 4px #1d1d1d;
    display: flex;
    justify-content: space-between;
    align-items:center;
}

.nav-list {
    display: flex;
    list-style: none;
}

.nav-item {
    margin-right: 20px;
    font-family: sans-serif;
    text-transform: uppercase;
    text-decoration: none;
    color: #1d1d1d;
}

.search-box {
  margin: 0 auto;
  background:linear-gradient(to right, #FF416C, #FF4B2B);
  color: #fff;
  border: none;
  border-radius: 35px;
  padding: 15px 50px;
  outline: none;
  font-size: 18px;
}
.search-box:focus {
    box-shadow: 0px 0px 10px grey;
}
.cards-container {
  width: 80%;
  max-height: 80vh;
  margin: 50px auto 0;
  overflow-y: auto;
  background: hsla(0, 0%, 100%, 0.1);
  border-radius: 30px;
}
.card-container {
  width: 80%;
  padding: 30px;
  text-align: center;
  margin: 30px auto;
  position: relative;
  box-shadow: 0 0 5px #444;
  border-radius: 10px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  display: flex;
  align-items: center;
}
.card-container:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  box-shadow: 0 0 15px #555;
}
img {
  border-radius: 5px;
  width: 50px;
}
.card-title {
  font-family: sans-serif;
  font-weight: 100;
}
.card-title,
small {
  color: #fff;
  width: 100%;
}
small {
  position: absolute;
  bottom: 5px;
  left: 0;
  text-decoration: underline;
  font-family: sans-serif;
}
.form-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.submit {
  display: none;
}

svg {
    width: 18px;
    position: relative;
    top: 5px;
    right: 45px;
}



.container {
    position: absolute;
    top:0px;
    left: 0px;
    width:100%;
    height: 100vh;
	display:flex;
	justify-content:center;
	align-items:center;
	flex-wrap:wrap;
    background: linear-gradient(#FF416C, #FF4B2B);
    animation: vanish .6s 4s ease-in-out forwards;
}

.inner-container {
	width:70px;
	height:50px;
	display:flex;
	justify-content:space-between;
	align-items:center;
    flex-wrap: wrap;
}

.logo-container {
    width:70px;
	height:40px;
	display:flex;
	justify-content:space-between;
	align-items:center;
    flex-wrap: wrap;
    margin-left: 10px;
}
.logo-bar {
    width:6px;
	height:100%;
	background: linear-gradient(to bottom, #FF416C 50%, #FF4B2B);
    border-radius:500% 5% 500% 5%;
}

.soundbar {
	width:5px;
	height:100%;
	background:#fff;
	box-shadow:0px 0px 5px black;
	border-radius:500% 5% 500% 5%;
}


.br1, .br3, .br5, .br7 {
	height:40%;
	animation: brBounce1 .3s 2 2s linear;
}

.br2, .br4, .br6 {
	height:100%;
	animation: brBounce2 .3s 2 2s linear;
}

.br1-2, .br3-2, .br5-2, .br7-2 {
    height:40%;
}
.br2-2, .br4-2, .br6-2 {
    height: 100%;
}
@keyframes brBounce1 {
	50% {
		height: 100%;
	}
	100% {
		height: 40%;
	}
}

@keyframes brBounce2 {
	50% {
		height: 40%;
	}
	100% {
		height: 100%;
	}
}

@keyframes vanish {
    100% {
        left: -100%;
    }
}
